.display-poi {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.checkbox-icon {
  width: 1em;
  height: 1em;
}

.onoffswitch {
  position: relative;
  width: 38px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
.onoffswitch-checkbox {
  position: absolute;
  opacity: 0;
  pointer-events: none;
}
.onoffswitch-label {
  display: block;
  overflow: hidden;
  cursor: pointer;
  border: 2px solid #ffffff;
  border-radius: 50px;
}
.onoffswitch-inner {
  display: block;
  width: 200%;
  margin-left: -100%;
  transition: margin 0.3s ease-in 0s;
}
.onoffswitch-inner:before,
.onoffswitch-inner:after {
  display: block;
  float: left;
  width: 50%;
  height: 14px;
  padding: 0;
  line-height: 14px;
  font-size: 10px;
  color: white;
  font-family: Trebuchet, Arial, sans-serif;
  font-weight: bold;
  box-sizing: border-box;
}
.onoffswitch-inner:before {
  content: "";
  padding-left: 5px;
  background-color: #32434e;
  color: #f1f1f1;
}
.onoffswitch-inner:after {
  content: "";
  padding-right: 5px;
  background-color: #32434e;
  color: #32434e;
  text-align: right;
}
.onoffswitch-switch {
  display: block;
  width: 20px;
  margin: -3px;
  background: #f1f1f1;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 20px;
  border: 2px solid #ffffff;
  border-radius: 50px;
  transition: all 0.3s ease-in 0s;
}
.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-inner {
  margin-left: 0;
}
.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-switch {
  right: 0px;
  background-color: #af1066;
}
